.wrapper {
    background-color: rgba(255, 255, 255, 0.08);
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    font-size: 16px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.9);
}

.upload {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 36px;
    min-width: 110px;

    svg {
        width: 20px;
        height: 20px;
    }

    span {
        font-size: 1.6rem;
        font-weight: 600;
        margin-left: 6px;
    }
}

.follow {
    border-width: 1px;
    border-style: solid;
    border-radius: 4px;
    color: rgba(255, 59, 92, 1);
    border-color: rgba(255, 59, 92, 1);
    background-color: rgba(255, 255, 255, 0.08);
    min-width: 88px;
    padding: 0 8px;
    cursor: pointer;
    font-family: var(--font-family);
    position: absolute;
    right: 0;
    top: -20%;

    &:hover {
        background-color: rgba(254, 44, 85, 0.06);
    }
}

.followFill {
    border-radius: 4px;
    border: none;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 59, 92);
    min-width: 208px;
    min-height: 28px;
    font-size: 1.8rem;
    line-height: 25px;
    font-weight: 600;
    padding: 6px 8px;
    font-family: var(--font-family);
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
        background: linear-gradient(
                0deg,
                rgba(0, 0, 0, 0.06),
                rgba(0, 0, 0, 0.06)
            ),
            rgb(254, 44, 85);
        cursor: pointer;
    }
}

.following {
    border-width: 1px;
    border-style: solid;
    border-radius: 4px;
    color: rgba(255, 255, 255, 0.9);
    border-color: rgba(255, 255, 255, 0);
    background-color: rgba(255, 255, 255, 0.08);
    min-width: 88px;
    padding: 0 10px;
    user-select: none;
    cursor: pointer;

    &:hover {
        border-color: rgb(208, 209, 211);
        background-color: rgba(18, 18, 18, 1);
    }
}

.login {
    display: block;
    line-height: 36px;
    border-width: 1px;
    border-style: solid;
    border-radius: 4px;
    color: rgba(255, 255, 255, 0.9);
    background-color: rgba(255, 59, 92, 1);
    border-color: rgba(255, 59, 92, 1);
    min-width: 110px;
    height: 36px;
    padding: 0;
    font-family: var(--font-family);
    font-size: 1.6rem;
    font-weight: 500;
    cursor: pointer;

    span {
        font-size: 1.6rem;
        font-weight: 600;
    }
}

.outline {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 36px;
    border-width: 1px;
    border-style: solid;
    border-radius: 4px;
    color: rgba(255, 59, 92, 1);
    background-color: rgba(255, 225, 225, 0.08);
    border-color: rgba(255, 59, 92, 1);
    height: 48px;
    padding: 0;
    font-family: var(--font-family);
    font-size: 1.6rem;
    font-weight: 600;
    cursor: pointer;
    text-align: center;
    margin-top: 20px;
}

.small {
    min-width: 88px;
    padding: 0 10px;
    height: 28px;
    line-height: 28px;
}

.menuItem {
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btnForm {
    background-color: transparent;
    cursor: pointer;
    color: rgba(255, 255, 255, 0.34);
    position: relative;
    width: 32px;
}
